import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CForm,
    CFormInput,
    CFormSelect,
    CPagination,
    CPaginationItem,
    CRow,
    CTable,
    CTableBody,
    CTableDataCell,
    CTableHead,
    CTableHeaderCell,
    CTableRow,
    CToast,
    CToastBody,
    CToastClose,
} from "@coreui/react";
import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { MdDelete, MdEdit } from "react-icons/md";
import { Link } from "react-router-dom";
const url = 'https://yog-seven.vercel.app'

const Measurment = () => {
    const [action, setAction] = useState(false)
    const [toast, setToast] = useState(false)
    const [id, setId] = useState()
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [email, setEmail] = useState('');
    const [category, setCategory] = useState('');
    const [address, setAddress] = useState('');
    const [company, setCompany] = useState('');
    const [result1, setResult1] = useState([])

    const [search1, setSearch1] = useState('')
    const [search2, setSearch2] = useState('')
    const [search3, setSearch3] = useState('')
    const [search4, setSearch4] = useState('')
    const [search5, setSearch5] = useState('')
    const [search6, setSearch6] = useState('')

    let user = JSON.parse(localStorage.getItem('user-info'))
    const [paging, setPaging] = useState(0);
    console.log(user);
    const token = user.token;
    const username = user.user.username;
    const centerCode = user.user.centerCode;
    useEffect(() => {
        searchData()
        getImpCall()
    }, [])

    function getImpCall() {
        axios.get(`${url}/fitnessDetail/all`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                setResult1(res.data.reverse())
                console.log(res.data);
            })
            .catch((error) => {
                console.error(error)
            })
    }
    const [details, setDetails] = useState({
        Measurement_Date: '',
        Weight: '',
        Height: '',
        BMI: '',
        Age: '',
        Fat: '',
        Neck: '',
        Shoulder: '',
        Chest: '',
        ArmsR: '',
        ArmsL: '',
        ForArms: '',
        Waist: '',
        Hips: '',
        ThighR: '',
        ThighL: '',
        CalfR: '',
        CalfL: '',
        Counseller: '',
        NextFollowup_Date: ''
    })
    const saveImpCall = () => {
        let data = {
            username: username, Member_ID: MemberId, Fullname: FullName, ContactNumber: ContactNumber,
            Weight: details.Weight, Height: details.Height, BMI: details.BMI, Age: details.Age, Fat: details.Fat,
            Neck: details.Neck, Shoulder: details.Shoulder, Chest: details.Chest, ArmsR: details.ArmsR, ArmsL: details.ArmsL, ForArms: details.ForArms,
            Waist: details.Waist, Hips: details.Hips, ThighR: details.ThighR, ThighL: details.ThighL, CalfR: details.CalfR, CalfL: details.CalfL,
            Counseller: details.Counseller, NextFollowup_Date: details.NextFollowup_Date
        }

        fetch(`${url}/fitnessDetail/create`, {
            method: "POST",
            headers: {
                "Authorization": `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        }).then((resp) => {
            resp.json().then(() => {
                setToast(true)
                setAction(false)
            })
        })
    }

    const saveUpdate = () => {

        let data = {
            username: username, Member_ID: MemberId, Fullname: FullName, ContactNumber: ContactNumber,
            Weight: details.Weight, Height: details.Height, BMI: details.BMI, Age: details.Age, Fat: details.Fat,
            Neck: details.Neck, Shoulder: details.Shoulder, Chest: details.Chest, ArmsR: details.ArmsR, ArmsL: details.ArmsL, ForArms: details.ForArms,
            Waist: details.Waist, Hips: details.Hips, ThighR: details.ThighR, ThighL: details.ThighL, CalfR: details.CalfR, CalfL: details.CalfL,
            Counseller: details.Counseller, NextFollowup_Date: details.NextFollowup_Date
        }

        fetch(`${url}/fitnessDetail/update/${id}`, {
            method: "POST",
            headers: {
                "Authorization": `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        }).then((resp) => {
            resp.json().then(() => {
                setToast(true)
                setAction(false)
                setInvoiceView(false)
            })
        })
    }
    function deleteCall(id) {
        if (confirm('Do you want to delete this')) {
            fetch(`${url}/fitnessDetail/delete/${id}`, {
                method: 'DELETE',
                headers: {
                    "Authorization": `Bearer ${token}`,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
            }).then((result) => {
                result.json().then(() => {
                    getImpCall()
                })
            })
        }
    }

    const handleUpdate = (id) => {
        setId(id)
        getUpdate(id)
        setAction(true)
        setInvoiceView(true)
    }
    const clear = () => {
        setId('')
        setName('')
        setPhone('')
        setEmail('')
        setCategory('')
        setAddress('')
        setCompany('')
        setInvoiceView(false)
    }

    function getUpdate(id) {
        axios.get(`${url}/fitnessDetail/${id}`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                console.log(res)
                setMemberId(res.data.Member_ID)
                setFullname(res.data.Fullname)
                setContactNumber(res.data.ContactNumber)
                inputUpdateEvent('Weight', res.data.Weight)
                inputUpdateEvent('Height', res.data.Height)
                inputUpdateEvent('Weight', res.data.Weight)
                inputUpdateEvent('BMI', res.data.BMI)
                inputUpdateEvent('Age', res.data.Age)
                inputUpdateEvent('Fat', res.data.Fat)
                inputUpdateEvent('Neck', res.data.Neck)
                inputUpdateEvent('Shoulder', res.data.Shoulder)
                inputUpdateEvent('Chest', res.data.Chest)
                inputUpdateEvent('ArmsR', res.data.ArmsR)
                inputUpdateEvent('ArmsL', res.data.ArmsL)
                inputUpdateEvent('ForArms', res.data.ForArms)
                inputUpdateEvent('Waist', res.data.Waist)
                inputUpdateEvent('Hips', res.data.Hips)
                inputUpdateEvent('ThighR', res.data.ThighR)
                inputUpdateEvent('ThighL', res.data.ThighL)
                inputUpdateEvent('CalfR', res.data.CalfR)
                inputUpdateEvent('CalfL', res.data.CalfL)
                inputUpdateEvent('Counseller', res.data.Counseller)
                inputUpdateEvent('NextFollowup_Date', moment(res.data.NextFollowup_Date).format("YYYY-MM-DD"))
            })
            .catch((error) => {
                console.error(error)
            })
    }
    function searchData() {
        const headers = {
            'Authorization': `Bearer ${token}`,
            'My-Custom-Header': 'foobar'
        };
        axios.get(`${url}/memberForm/all`, { headers },
        ).then((resp) => {
            console.log(resp.data);
            setclientArr(resp.data);

        })
            .catch((error) => {
                console.error(error)
            })

    }

    const [MemberId, setMemberId] = useState('')
    const [FullName, setFullname] = useState('')
    const [ContactNumber, setContactNumber] = useState('')
    const [invoiceView, setInvoiceView] = useState(false)
    const [search, setSearch] = useState('')
    const [searchType, setSearchType] = useState('')
    const [clientArr, setclientArr] = useState([])
    function searchSingle(id) {
        const headers = {
            'Authorization': `Bearer ${token}`,
            'My-Custom-Header': 'foobar'
        };
        axios.get(`${url}/memberForm/${id}`, { headers },
        ).then((resp) => {
            console.log(resp.data);
            setMemberId(resp.data._id)
            setFullname(resp.data.Fullname)
            setContactNumber(resp.data.ContactNumber)
            setInvoiceView(true)
        })
            .catch((error) => {
                console.error(error)
            })

    }
    console.log(clientArr);
    const inputEvent = (e) => {
        const { name, value } = e.target;
        setDetails((preVal) => {
            return {
                ...preVal,
                [name]: value
            }
        })
    }
    const inputUpdateEvent = (name, value) => {
        setDetails((preVal) => {
            return {
                ...preVal,
                [name]: value
            }
        })
    }
    return (
        <CRow className='d-flex mb-2'>
            <CCol lg={9} sm={6} className='mb-2'>
                <CToast autohide={true} visible={toast} color='success' className="align-items-center">
                    <div className="d-flex">
                        <CToastBody style={{ color: 'white' }}>Successfully Submitted.</CToastBody>
                        <CToastClose className="me-2 m-auto" />
                    </div>
                </CToast>
            </CCol>
            <CCol lg={3} sm={6} className='mb-2'>
                <CButton className="float-end" onClick={() => { setAction(!action), clear() }}>{action ? 'Close' : 'Add New'}</CButton>
            </CCol>
            {action &&

                <>
                    {!invoiceView &&
                        <CCard className='mb-2'>
                            <CCardBody>
                                <CRow>
                                    <CCol>
                                        <CFormSelect
                                            className="mb-1"
                                            aria-label="Select Service Name"
                                            value={searchType}
                                            onChange={(e) => setSearchType(e.target.value)}
                                        >
                                            <option>Select</option>
                                            <option value='Fullname'>Name</option>
                                            <option value='ContactNumber'>Mobile</option>
                                        </CFormSelect>
                                    </CCol>
                                    <CCol>
                                        <CFormInput
                                            type={searchType === 'Fullname' ? 'text' : 'number'}
                                            value={search}
                                            onChange={(e) => { setSearch(e.target.value), setInvoiceView(false) }}
                                            style={{ minWidth: "100px" }}
                                            aria-describedby="exampleFormControlInputHelpInline"
                                        />
                                    </CCol>
                                    <CCol>
                                        <CButton onClick={() => searchData()}>Search</CButton>
                                    </CCol>
                                </CRow >

                                <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} responsive>
                                    <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }} >
                                        <CTableRow >
                                            <CTableHeaderCell>Client Name</CTableHeaderCell>
                                            <CTableHeaderCell>Number</CTableHeaderCell>
                                            <CTableHeaderCell>Action</CTableHeaderCell>
                                        </CTableRow>
                                    </CTableHead>
                                    <CTableBody>
                                        {clientArr?.filter((list) => list.username === username && (searchType === 'Fullname' ? list.Fullname.toLowerCase().includes(search.toLowerCase()) : list.ContactNumber?.toString().includes(search?.toString()))).map((item, index) => (
                                            item.username === username && (
                                                search != '' && (
                                                    <CTableRow key={index}>
                                                        <CTableDataCell>{item.Fullname}</CTableDataCell>
                                                        <CTableDataCell>{item.ContactNumber}</CTableDataCell>
                                                        <CTableDataCell><label style={{ color: 'blue', cursor: 'pointer' }} onClick={() => searchSingle(item._id)}>Select</label></CTableDataCell>
                                                    </CTableRow>
                                                )
                                            )
                                        ))}
                                    </CTableBody>
                                </CTable>
                            </CCardBody>
                        </CCard>
                    }
                    {invoiceView &&
                        <CCard className="mt-2 mb-2" >
                            <CCardBody>
                                <CForm>
                                    <CRow>
                                        <CCol lg={6} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type="text"
                                                name="name"
                                                id="exampleFormControlInput1"
                                                label="Name "
                                                value={FullName}
                                                onChange={(e) => setFullname(e.target.value)}
                                                placeholder="Enter Name"
                                            />
                                        </CCol>

                                        <CCol lg={6} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Mobile Number"
                                                value={ContactNumber}
                                                onChange={(e) => setContactNumber(e.target.value)}
                                                placeholder="Enter Mobile Number"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                name="Weight"
                                                value={details.Weight}
                                                onChange={(e) => inputEvent(e)}
                                                label="Weight"
                                                placeholder="Enter Weight"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Height"
                                                name="Height"
                                                value={details.Height}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Height"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="BMI"
                                                name="BMI"
                                                value={details.BMI}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter BMI"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Age"
                                                name="Age"
                                                value={details.Age}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Age"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Fat"
                                                name="Fat"
                                                value={details.Fat}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Fat"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Neck"
                                                name="Neck"
                                                value={details.Neck}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Neck"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Shoulder"
                                                name="Shoulder"
                                                value={details.Shoulder}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Shoulder"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Chest"
                                                name="Chest"
                                                value={details.Chest}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Chest"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Arms(R)"
                                                name="ArmsR"
                                                value={details.ArmsR}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Arms(R)"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Arms(L)"
                                                name="ArmsL"
                                                value={details.ArmsL}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Arms(L)"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="ForArms"
                                                name="ForArms"
                                                value={details.ForArms}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter ForArms"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Waist"
                                                name="Waist"
                                                value={details.Waist}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Waist"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Hips"
                                                name="Hips"
                                                value={details.Hips}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Hips"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Thigh(R)"
                                                name="ThighR"
                                                value={details.ThighR}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Thigh(R)"
                                            />
                                        </CCol>


                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Thigh(L)"
                                                name="ThighL"
                                                value={details.ThighL}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Thigh(L)"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Calf(R)"
                                                name="CalfR"
                                                value={details.CalfR}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter CalfR"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Calf(L)"
                                                name="CalfL"
                                                value={details.CalfL}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Calf(L)"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='text'
                                                id="exampleFormControlInput1"
                                                label="Counseller"
                                                name="Counseller"
                                                value={details.Counseller}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Counseller"
                                            />
                                        </CCol>

                                        <CCol lg={6} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='date'
                                                id="exampleFormControlInput1"
                                                label="Next FollowUp Date"
                                                name="NextFollowup_Date"
                                                value={details.NextFollowup_Date}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Next FollowUp Date"
                                            />
                                        </CCol>

                                        <CCol className="mt-4">
                                            {id
                                                ?
                                                <CButton onClick={() => saveUpdate()} >update</CButton>
                                                :
                                                <CButton onClick={() => { saveImpCall() }} >Save</CButton>
                                            }
                                        </CCol>
                                    </CRow>
                                </CForm>
                            </CCardBody>
                        </CCard>
                    }
                </>
            }
            <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} hover responsive>
                <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }} >
                    <CTableRow >
                        <CTableHeaderCell>Sr.No</CTableHeaderCell>
                        <CTableHeaderCell>Measurement Date</CTableHeaderCell>
                        <CTableHeaderCell>Member ID</CTableHeaderCell>
                        <CTableHeaderCell>Name</CTableHeaderCell>
                        <CTableHeaderCell>Contact</CTableHeaderCell>
                        <CTableHeaderCell>Weight</CTableHeaderCell>
                        <CTableHeaderCell>Height</CTableHeaderCell>
                        <CTableHeaderCell>BMI</CTableHeaderCell>
                        <CTableHeaderCell>Age</CTableHeaderCell>
                        <CTableHeaderCell>Fat</CTableHeaderCell>
                        <CTableHeaderCell>Neck</CTableHeaderCell>
                        <CTableHeaderCell>Shoulder</CTableHeaderCell>
                        <CTableHeaderCell>Chest</CTableHeaderCell>
                        <CTableHeaderCell>Arms(R)</CTableHeaderCell>
                        <CTableHeaderCell>Arms(L)</CTableHeaderCell>
                        <CTableHeaderCell>ForArms</CTableHeaderCell>
                        <CTableHeaderCell>Waist</CTableHeaderCell>
                        <CTableHeaderCell>Hips</CTableHeaderCell>
                        <CTableHeaderCell>Thigh(R)</CTableHeaderCell>
                        <CTableHeaderCell>Thigh(L)</CTableHeaderCell>
                        <CTableHeaderCell>Calf(R)</CTableHeaderCell>
                        <CTableHeaderCell>Calf(L)</CTableHeaderCell>
                        <CTableHeaderCell>Counseller</CTableHeaderCell>
                        <CTableHeaderCell>Next FollowUp Date</CTableHeaderCell>
                        <CTableHeaderCell>Action</CTableHeaderCell>
                    </CTableRow>
                </CTableHead>
                <CTableBody>
                    <CTableRow>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "60px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "60px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "90px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                value={search1}
                                onChange={(e) => setSearch1(e.target.value)}
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search2}
                                onChange={(e) => setSearch2(e.target.value)}
                                type="number"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "100px" }}
                                value={search3}
                                disabled
                                onChange={(e) => setSearch3(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "200px" }}
                                value={search4}
                                disabled
                                onChange={(e) => setSearch4(e.target.value)}
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search5}
                                disabled
                                onChange={(e) => setSearch5(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search6}
                                disabled
                                onChange={(e) => setSearch6(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                    </CTableRow>
                    {result1.slice(paging * 10, paging * 10 + 10).filter((list) =>
                        list.username === username && list.Fullname.toLowerCase().includes(search1.toLowerCase()) && list.ContactNumber.toString().includes(search2.toString())
                    ).map((item, index) => (
                        <CTableRow key={index}>
                            <CTableDataCell>{index + 1 + (paging * 10)}</CTableDataCell>
                            <CTableDataCell>{moment(item.createdAt).format("MM-DD-YYYY")}</CTableDataCell>
                            <CTableDataCell>{item.Member_ID}</CTableDataCell>
                            <CTableDataCell><Link index={-1} style={{ textDecoration: 'none' }} to={`/clients/member-details/${item.Member_ID}/1`} target="_black">{item.Fullname}</Link></CTableDataCell>
                            <CTableDataCell>{item.ContactNumber}</CTableDataCell>
                            <CTableDataCell>{item.Weight}</CTableDataCell>
                            <CTableDataCell>{item.Height}</CTableDataCell>
                            <CTableDataCell>{item.BMI}</CTableDataCell>
                            <CTableDataCell>{item.Age}</CTableDataCell>
                            <CTableDataCell>{item.Fat}</CTableDataCell>
                            <CTableDataCell>{item.Neck}</CTableDataCell>
                            <CTableDataCell>{item.Shoulder}</CTableDataCell>
                            <CTableDataCell>{item.Chest}</CTableDataCell>
                            <CTableDataCell>{item.ArmsR}</CTableDataCell>
                            <CTableDataCell>{item.ArmsL}</CTableDataCell>
                            <CTableDataCell>{item.ForArms}</CTableDataCell>
                            <CTableDataCell>{item.Waist}</CTableDataCell>
                            <CTableDataCell>{item.Hips}</CTableDataCell>
                            <CTableDataCell>{item.ThighR}</CTableDataCell>
                            <CTableDataCell>{item.ThighL}</CTableDataCell>
                            <CTableDataCell>{item.CalfR}</CTableDataCell>
                            <CTableDataCell>{item.CalfL}</CTableDataCell>
                            <CTableDataCell>{item.Counseller}</CTableDataCell>
                            <CTableDataCell>{moment(item.NextFollowup_Date).format("MM-DD-YYYY")}</CTableDataCell>
                            <CTableDataCell className='text-center'><MdEdit id={item._id} style={{ fontSize: '35px', cursor: 'pointer', markerStart: '10px' }} onClick={() => handleUpdate(item._id)} size='20px' /> <MdDelete style={{ cursor: 'pointer', markerStart: '10px', marginLeft: "5px" }} onClick={() => deleteCall(item._id)} size='20px' /></CTableDataCell>
                        </CTableRow>
                    ))}
                </CTableBody>
            </CTable>
            <CPagination aria-label="Page navigation example" align="center" className='mt-2'>
                <CPaginationItem aria-label="Previous" disabled={paging != 0 ? false : true} onClick={() => paging > 0 && setPaging(paging - 1)}>
                    <span aria-hidden="true">&laquo;</span>
                </CPaginationItem>
                <CPaginationItem active onClick={() => setPaging(0)}>{paging + 1}</CPaginationItem>
                {result1.filter((list) =>
                    list.username === username && list.Fullname.includes(search1) && list.ContactNumber.toString().includes(search2.toString())
                ).length > (paging + 1) * 10 && <CPaginationItem onClick={() => setPaging(paging + 1)} >{paging + 2}</CPaginationItem>}
                {result1.filter((list) =>
                    list.username === username && list.Fullname.includes(search1) && list.ContactNumber.toString().includes(search2.toString())
                ).length > (paging + 2) * 10 && <CPaginationItem onClick={() => setPaging(paging + 2)}>{paging + 3}</CPaginationItem>}
                {result1.filter((list) =>
                    list.username === username && list.Fullname.includes(search1) && list.ContactNumber.toString().includes(search2.toString())
                ).length > (paging + 1) * 10 ?
                    <CPaginationItem aria-label="Next" onClick={() => setPaging(paging + 1)}>
                        <span aria-hidden="true">&raquo;</span>
                    </CPaginationItem>
                    : <CPaginationItem disabled aria-label="Next" onClick={() => setPaging(paging + 1)}>
                        <span aria-hidden="true">&raquo;</span>
                    </CPaginationItem>
                }
            </CPagination>
        </CRow>

    );
};


export default Measurment;