import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CForm,
    CFormInput,
    CFormSelect,
    CPagination,
    CPaginationItem,
    CRow,
    CTable,
    CTableBody,
    CTableDataCell,
    CTableHead,
    CTableHeaderCell,
    CTableRow,
    CToast,
    CToastBody,
    CToastClose,
} from "@coreui/react";
import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { MdDelete, MdEdit } from "react-icons/md";
import { Link } from "react-router-dom";
const url = 'https://yog-seven.vercel.app'

const Diet = () => {
    const [action, setAction] = useState(false)
    const [toast, setToast] = useState(false)
    const [id, setId] = useState()
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [email, setEmail] = useState('');
    const [category, setCategory] = useState('');
    const [address, setAddress] = useState('');
    const [company, setCompany] = useState('');
    const [result1, setResult1] = useState([])

    const [search1, setSearch1] = useState('')
    const [search2, setSearch2] = useState('')
    const [search3, setSearch3] = useState('')
    const [search4, setSearch4] = useState('')
    const [search5, setSearch5] = useState('')
    const [search6, setSearch6] = useState('')

    let user = JSON.parse(localStorage.getItem('user-info'))
    const [paging, setPaging] = useState(0);
    console.log(user);
    const token = user.token;
    const username = user.user.username;
    const centerCode = user.user.centerCode;
    useEffect(() => {
        searchData()
        getImpCall()
    }, [])

    function getImpCall() {
        axios.get(`${url}/dietDetail/all`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                setResult1(res.data.reverse())
                console.log(res.data);
            })
            .catch((error) => {
                console.error(error)
            })
    }
    const [details, setDetails] = useState({
        DietStartDate: '',
        Gender: '',
        Purpose: '',
        DietEndDate: '',
        ExpertAdvice: '',
        DietitianName: '',
    })
    const saveImpCall = () => {
        let data = {
            diet_Id: "D_ID" + result1.length,
            username: username, Member_ID: MemberId, Fullname: FullName, ContactNumber: ContactNumber,
            DietStartDate: details.DietStartDate,
            Gender: details.Gender, Purpose: details.Purpose,
            DietEndDate: details.DietEndDate, ExpertAdvice: details.ExpertAdvice,
            DietitianName: details.DietitianName,
        }

        fetch(`${url}/dietDetail/create`, {
            method: "POST",
            headers: {
                "Authorization": `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        }).then((resp) => {
            resp.json().then(() => {
                setToast(true)
                setAction(false)
                getImpCall()
            })
        })
    }

    const saveUpdate = () => {

        let data = {
            username: username, Member_ID: MemberId, Fullname: FullName,
            ContactNumber: ContactNumber,
            DietStartDate: details.DietStartDate,
            Gender: details.Gender, Purpose: details.Purpose,
            DietEndDate: details.DietEndDate, ExpertAdvice: details.ExpertAdvice,
            DietitianName: details.DietitianName,
        }

        fetch(`${url}/dietDetail/update/${id}`, {
            method: "POST",
            headers: {
                "Authorization": `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        }).then((resp) => {
            resp.json().then(() => {
                setToast(true)
                setAction(false)
                setInvoiceView(false)
            })
        })
    }
    function deleteCall(id) {
        if (confirm('Do you want to delete this')) {
            fetch(`${url}/dietDetail/delete/${id}`, {
                method: 'DELETE',
                headers: {
                    "Authorization": `Bearer ${token}`,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
            }).then((result) => {
                result.json().then(() => {
                    getImpCall()
                })
            })
        }
    }

    const handleUpdate = (id) => {
        setId(id)
        getUpdate(id)
        setAction(true)
        setInvoiceView(true)
    }
    const clear = () => {
        setId('')
        setName('')
        setPhone('')
        setEmail('')
        setCategory('')
        setAddress('')
        setCompany('')
        setInvoiceView(false)
    }

    function getUpdate(id) {
        axios.get(`${url}/dietDetail/${id}`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                console.log(res)
                setMemberId(res.data.Member_ID)
                setFullname(res.data.Fullname)
                setContactNumber(res.data.ContactNumber)
                inputUpdateEvent('Gender', res.data.Gender)
                inputUpdateEvent('Purpose', res.data.Purpose)
                inputUpdateEvent('DietEndDate', moment(res.data.DietEndDate).format("YYYY-MM-DD"))
                inputUpdateEvent('DietStartDate', moment(res.data.DietStartDate).format("YYYY-MM-DD"))
                inputUpdateEvent('ExpertAdvice', res.data.ExpertAdvice)
                inputUpdateEvent('DietitianName', res.data.DietitianName)
                inputUpdateEvent('NextFollowup_Date', moment(res.data.NextFollowup_Date).format("YYYY-MM-DD"))
            })
            .catch((error) => {
                console.error(error)
            })
    }
    function searchData() {
        const headers = {
            'Authorization': `Bearer ${token}`,
            'My-Custom-Header': 'foobar'
        };
        axios.get(`${url}/memberForm/all`, { headers },
        ).then((resp) => {
            console.log(resp.data);
            setclientArr(resp.data);

        })
            .catch((error) => {
                console.error(error)
            })

    }

    const [MemberId, setMemberId] = useState('')
    const [FullName, setFullname] = useState('')
    const [ContactNumber, setContactNumber] = useState('')
    const [invoiceView, setInvoiceView] = useState(false)
    const [search, setSearch] = useState('')
    const [searchType, setSearchType] = useState('')
    const [clientArr, setclientArr] = useState([])
    function searchSingle(id) {
        const headers = {
            'Authorization': `Bearer ${token}`,
            'My-Custom-Header': 'foobar'
        };
        axios.get(`${url}/memberForm/${id}`, { headers },
        ).then((resp) => {
            console.log(resp.data);
            setMemberId(resp.data._id)
            setFullname(resp.data.Fullname)
            setContactNumber(resp.data.ContactNumber)
            setDetails({ Gender: resp.data.Gender })
            setInvoiceView(true)
        })
            .catch((error) => {
                console.error(error)
            })

    }
    console.log(result1);
    const inputEvent = (e) => {
        const { name, value } = e.target;
        setDetails((preVal) => {
            return {
                ...preVal,
                [name]: value
            }
        })
    }
    const inputUpdateEvent = (name, value) => {
        setDetails((preVal) => {
            return {
                ...preVal,
                [name]: value
            }
        })
    }
    return (
        <CRow className='d-flex mb-2'>
            <CCol lg={9} sm={6} className='mb-2'>
                <CToast autohide={true} visible={toast} color='success' className="align-items-center">
                    <div className="d-flex">
                        <CToastBody style={{ color: 'white' }}>Successfully Submitted.</CToastBody>
                        <CToastClose className="me-2 m-auto" />
                    </div>
                </CToast>
            </CCol>
            <CCol lg={3} sm={6} className='mb-2'>
                <CButton className="float-end" onClick={() => { setAction(!action), clear() }}>{action ? 'Close' : 'Add New'}</CButton>
            </CCol>
            {action &&

                <>
                    {!invoiceView &&
                        <CCard className='mb-2'>
                            <CCardBody>
                                <CRow>
                                    <CCol>
                                        <CFormSelect
                                            className="mb-1"
                                            aria-label="Select Service Name"
                                            value={searchType}
                                            onChange={(e) => setSearchType(e.target.value)}
                                        >
                                            <option>Select</option>
                                            <option value='Fullname'>Name</option>
                                            <option value='ContactNumber'>Mobile</option>
                                        </CFormSelect>
                                    </CCol>
                                    <CCol>
                                        <CFormInput
                                            type={searchType === 'Fullname' ? 'text' : 'number'}
                                            value={search}
                                            onChange={(e) => { setSearch(e.target.value), setInvoiceView(false) }}
                                            style={{ minWidth: "100px" }}
                                            aria-describedby="exampleFormControlInputHelpInline"
                                        />
                                    </CCol>
                                    <CCol>
                                        <CButton onClick={() => searchData()}>Search</CButton>
                                    </CCol>
                                </CRow >

                                <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} responsive>
                                    <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }} >
                                        <CTableRow >
                                            <CTableHeaderCell>Client Name</CTableHeaderCell>
                                            <CTableHeaderCell>Number</CTableHeaderCell>
                                            <CTableHeaderCell>Action</CTableHeaderCell>
                                        </CTableRow>
                                    </CTableHead>
                                    <CTableBody>
                                        {clientArr?.filter((list) => list.username === username && (searchType === 'Fullname' ? list.Fullname.toLowerCase().includes(search.toLowerCase()) : list.ContactNumber?.toString().includes(search?.toString()))).map((item, index) => (
                                            item.username === username && (
                                                search != '' && (
                                                    <CTableRow key={index}>
                                                        <CTableDataCell>{item.Fullname}</CTableDataCell>
                                                        <CTableDataCell>{item.ContactNumber}</CTableDataCell>
                                                        <CTableDataCell><label style={{ color: 'blue', cursor: 'pointer' }} onClick={() => searchSingle(item._id)}>Select</label></CTableDataCell>
                                                    </CTableRow>
                                                )
                                            )
                                        ))}
                                    </CTableBody>
                                </CTable>
                            </CCardBody>
                        </CCard>
                    }
                    {invoiceView &&
                        <CCard className="mt-2 mb-2" >
                            <CCardBody>
                                <CForm>
                                    <CRow>
                                        <CCol lg={4} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type="text"
                                                name="name"
                                                id="exampleFormControlInput1"
                                                label="Name "
                                                value={FullName}
                                                onChange={(e) => setFullname(e.target.value)}
                                                placeholder="Enter Name"
                                            />
                                        </CCol>

                                        <CCol lg={4} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='number'
                                                id="exampleFormControlInput1"
                                                label="Mobile Number"
                                                value={ContactNumber}
                                                onChange={(e) => setContactNumber(e.target.value)}
                                                placeholder="Enter Mobile Number"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='text'
                                                id="exampleFormControlInput1"
                                                name="Gender"
                                                disabled
                                                value={details.Gender}
                                                onChange={(e) => inputEvent(e)}
                                                label="Weight"
                                                placeholder="Enter Gender"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='date'
                                                id="exampleFormControlInput1"
                                                label="Diet Start Date"
                                                name="DietStartDate"
                                                value={details.DietStartDate}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Diet Start Date"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='date'
                                                id="exampleFormControlInput1"
                                                label="Diet End Date"
                                                name="DietEndDate"
                                                value={details.DietEndDate}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Diet End Date"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='text'
                                                id="exampleFormControlInput1"
                                                label="Purpose"
                                                name="Purpose"
                                                value={details.Purpose}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Purpose"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='text'
                                                id="exampleFormControlInput1"
                                                label="Expert Advice"
                                                name="ExpertAdvice"
                                                value={details.ExpertAdvice}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Expert Advice"
                                            />
                                        </CCol>

                                        <CCol lg={3} sm={6} >
                                            <CFormInput
                                                className="mb-1"
                                                type='text'
                                                id="exampleFormControlInput1"
                                                label="Dietitian Name"
                                                name="DietitianName"
                                                value={details.DietitianName}
                                                onChange={(e) => inputEvent(e)}
                                                placeholder="Enter Dietitian Name"
                                            />
                                        </CCol>

                                        <CCol className="mt-4">
                                            {id
                                                ?
                                                <CButton onClick={() => saveUpdate()} >update</CButton>
                                                :
                                                <CButton onClick={() => { saveImpCall() }} >Save</CButton>
                                            }
                                        </CCol>
                                    </CRow>
                                </CForm>
                            </CCardBody>
                        </CCard>
                    }
                </>
            }
            <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} hover responsive>
                <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }} >
                    <CTableRow >
                        <CTableHeaderCell>Sr.No</CTableHeaderCell>
                        <CTableHeaderCell>DietStartDate</CTableHeaderCell>
                        <CTableHeaderCell>Diet Id</CTableHeaderCell>
                        <CTableHeaderCell>Name</CTableHeaderCell>
                        <CTableHeaderCell>Contact</CTableHeaderCell>
                        <CTableHeaderCell>Gender</CTableHeaderCell>
                        <CTableHeaderCell>Purpose</CTableHeaderCell>
                        <CTableHeaderCell>DietEndDate</CTableHeaderCell>
                        <CTableHeaderCell>Expert Advice</CTableHeaderCell>
                        <CTableHeaderCell>DietitianName</CTableHeaderCell>
                        <CTableHeaderCell>Action</CTableHeaderCell>
                    </CTableRow>
                </CTableHead>
                <CTableBody>
                    <CTableRow>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "60px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "60px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "90px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                value={search1}
                                onChange={(e) => setSearch1(e.target.value)}
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search2}
                                onChange={(e) => setSearch2(e.target.value)}
                                type="number"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "100px" }}
                                value={search3}
                                onChange={(e) => setSearch3(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "200px" }}
                                value={search4}
                                onChange={(e) => setSearch4(e.target.value)}
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                type="text"
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search5}
                                onChange={(e) => setSearch5(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                style={{ minWidth: "120px" }}
                                value={search6}
                                onChange={(e) => setSearch6(e.target.value)}
                                type="text"
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                        <CTableDataCell>
                            <CFormInput
                                className="mb-1"
                                type="text"
                                style={{ minWidth: "120px" }}
                                disabled
                                aria-describedby="exampleFormControlInputHelpInline"
                            />
                        </CTableDataCell>
                    </CTableRow>
                    {result1?.slice(paging * 10, paging * 10 + 10)?.filter((list) =>
                        list.username === username && list?.Fullname?.toLowerCase().includes(search1?.toLowerCase()) && list?.ContactNumber?.toString().includes(search2?.toString()) && list?.Gender?.toLowerCase().includes(search3?.toLowerCase())
                        && list?.Purpose?.toLowerCase().includes(search4?.toLowerCase()) && list?.ExpertAdvice?.toLowerCase().includes(search5?.toLowerCase()) && list?.DietitianName?.toLowerCase().includes(search6?.toLowerCase())
                    )?.map((item, index) => (
                        <CTableRow key={index}>
                            <CTableDataCell>{index + 1 + (paging * 10)}</CTableDataCell>
                            <CTableDataCell>{moment(item.DietStartDate).format("MM-DD-YYYY")}</CTableDataCell>
                            <CTableDataCell>{item.diet_Id}</CTableDataCell>
                            <CTableDataCell><Link index={-1} style={{ textDecoration: 'none' }} to={`/clients/member-details/${item.Member_ID}/1`} target="_black">{item.Fullname}</Link></CTableDataCell>
                            <CTableDataCell>{item.ContactNumber}</CTableDataCell>
                            <CTableDataCell>{item.Gender}</CTableDataCell>
                            <CTableDataCell>{item.Purpose}</CTableDataCell>
                            <CTableDataCell>{moment(item.DietEndDate).format("MM-DD-YYYY")}</CTableDataCell>
                            <CTableDataCell>{item.ExpertAdvice}</CTableDataCell>
                            <CTableDataCell>{item.DietitianName}</CTableDataCell>
                            <CTableDataCell className='text-center'><MdEdit id={item._id} style={{ fontSize: '35px', cursor: 'pointer', markerStart: '10px' }} onClick={() => handleUpdate(item._id)} size='20px' /> <MdDelete style={{ cursor: 'pointer', markerStart: '10px', marginLeft: "5px" }} onClick={() => deleteCall(item._id)} size='20px' /></CTableDataCell>
                        </CTableRow>
                    ))}
                </CTableBody>
            </CTable>
            <CPagination aria-label="Page navigation example" align="center" className='mt-2'>
                <CPaginationItem aria-label="Previous" disabled={paging != 0 ? false : true} onClick={() => paging > 0 && setPaging(paging - 1)}>
                    <span aria-hidden="true">&laquo;</span>
                </CPaginationItem>
                <CPaginationItem active onClick={() => setPaging(0)}>{paging + 1}</CPaginationItem>
                {result1?.filter((list) =>
                    list.username === username && list?.Fullname?.toLowerCase().includes(search1?.toLowerCase()) && list?.ContactNumber?.toString().includes(search2?.toString()) && list?.Gender?.toLowerCase().includes(search3?.toLowerCase())
                    && list?.Purpose?.toLowerCase().includes(search4?.toLowerCase()) && list?.ExpertAdvice?.toLowerCase().includes(search5?.toLowerCase()) && list?.DietitianName?.toLowerCase().includes(search6?.toLowerCase())
                ).length > (paging + 1) * 10 && <CPaginationItem onClick={() => setPaging(paging + 1)} >{paging + 2}</CPaginationItem>}

                {result1?.filter((list) =>
                    list.username === username && list?.Fullname?.toLowerCase().includes(search1?.toLowerCase()) && list?.ContactNumber?.toString().includes(search2?.toString()) && list?.Gender?.toLowerCase().includes(search3?.toLowerCase())
                    && list?.Purpose?.toLowerCase().includes(search4?.toLowerCase()) && list?.ExpertAdvice?.toLowerCase().includes(search5?.toLowerCase()) && list?.DietitianName?.toLowerCase().includes(search6?.toLowerCase())
                ).length > (paging + 2) * 10 && <CPaginationItem onClick={() => setPaging(paging + 2)}>{paging + 3}</CPaginationItem>}
                {result1?.filter((list) =>
                    list.username === username && list?.Fullname?.toLowerCase().includes(search1?.toLowerCase()) && list?.ContactNumber?.toString().includes(search2?.toString()) && list?.Gender?.toLowerCase().includes(search3?.toLowerCase())
                    && list?.Purpose?.toLowerCase().includes(search4?.toLowerCase()) && list?.ExpertAdvice?.toLowerCase().includes(search5?.toLowerCase()) && list?.DietitianName?.toLowerCase().includes(search6?.toLowerCase())
                ).length > (paging + 1) * 10 ?
                    <CPaginationItem aria-label="Next" onClick={() => setPaging(paging + 1)}>
                        <span aria-hidden="true">&raquo;</span>
                    </CPaginationItem>
                    : <CPaginationItem disabled aria-label="Next" onClick={() => setPaging(paging + 1)}>
                        <span aria-hidden="true">&raquo;</span>
                    </CPaginationItem>
                }
            </CPagination>
        </CRow>

    );
};


export default Diet;